.page-header {
  width: 100%;
  background-color: @white;
}

.page-header__wrapper {
  padding: 15px 20px 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}

.page-header__logo {
  width: 195px;
  margin-bottom: 15px;
}

.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.8;
}

.page-header__logo:active {
  opacity: 0.6;
}

@media (min-width: @tablet-width) {
  .page-header__wrapper {
    flex-direction: row;
    padding: 0 30px;
    width: 768px;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    &--inverted {
    background: url("../img/index-background-desktop@1x.jpg") no-repeat top -48px left 112%;

    @media
    (min-resolution: @retina-dpi),
    (min-resolution: @retina-dppx) {
      background: url("../img/index-background-desktop@2x.jpg") no-repeat top -48px left 112%;
      background-size: 720px auto;
    }
    }
  }

  .page-header__wrapper {
    padding: 0 40px;
    width: 1300px;
  }
}
