.introduction {
  width: 100%;
  background: @white url("../img/index-background-mobile@1x.jpg") no-repeat top center;

  @media
  (min-resolution: @retina-dpi),
  (min-resolution: @retina-dppx) {
    background: @white url("../img/index-background-mobile@2x.jpg") no-repeat center top;
    background-size: 320px auto;
  }
}

.introduction__wrapper {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-bottom: 23px;
  width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
}

.introduction__header {
  color: @white;
  width: 280px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 25px auto 25px;
}

.introduction__tagline {
  color: @white;
  width: 280px;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  margin: 0 auto 30px;
}

.introduction__img {
  width: 280px;
  margin: 0 auto 5px;
}

.introduction__button {
  margin: 0 auto 22px;
}

@media (min-width: @tablet-width) {
  .introduction {
    background: @white url("../img/index-can-tablet@1x.jpg") no-repeat bottom  center;

    @media
    (min-resolution: @retina-dpi),
    (min-resolution: @retina-dppx) {
      background: @white url("../img/index-can-tablet@2x.jpg") no-repeat bottom  center;
      background-size: 710px auto;
    }
  }
  .introduction__wrapper {
    padding: 0 30px;
    text-align: left;
    width: 768px;
  }

  .introduction__header {
    width: 437px;
    color: @black;
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    margin-left: 90px;
    margin-top: 50px;
    margin-bottom: 40px;
  }

  .introduction__tagline {
    width: 295px;
    color: @black;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    margin-left: 90px;
    margin-bottom: 50px;
  }

  .introduction__img {
    display: none;
  }

  .introduction__button {
    width: 245px;
    z-index: 2;
    margin-bottom: 530px;
    margin-left: 90px;
    font-size: 20px;
    line-height: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

@media (min-width: @desktop-width) {
  .introduction {
    background: url("../img/index-can-desktop@1x.png") no-repeat left 66% top 21px,
    url("../img/index-background-desktop@1x.jpg") no-repeat top -120px left 112%;

    @media
    (min-resolution: @retina-dpi),
    (min-resolution: @retina-dppx) {
      background: @white url("../img/index-can-desktop@2x.png") no-repeat left 66% top 21px,
      url("../img/index-background-desktop@2x.jpg") no-repeat top -120px left 112%;
    }
  }

  .introduction__wrapper {
    padding: 0 40px;
    text-align: left;
    width: 1300px;
  }

  .introduction__header {
    margin-left: 81px;
    margin-top: 102px;
    margin-bottom: 41px;
  }

  .introduction__tagline {
    margin-left: 81px;
    margin-bottom: 53px;
  }

  .introduction__button {
    margin-bottom: 270px;
    margin-left: 81px;
    padding-bottom: 16px;
  }
}
